:root{--color-white: #ffffff;--color-dark-blue: #36394a;--color-blue: #4e75ff;--color-blue-rgb: 92, 128, 253;--color-blue-alpha-08: rgba(var(--color-blue-rgb), 0.08);--color-blue-alpha-100: rgba(var(--color-blue-rgb), 1);--color-dark-gray: #f7f7f7;--color-gray: #6e7076;--color-light-gray: #9e9da4}html,body{max-width:100vw;overflow-x:hidden}body{font-family:Arial,Helvetica,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:var(--color-dark-gray)}*{box-sizing:border-box;padding:0;margin:0}a{color:inherit;text-decoration:none}.container{max-width:1256px;width:100%;padding-left:64px;padding-right:64px;margin:0 auto}